'use strict';

/**
* Return default ndarray settings.
*
* @module @stdlib/ndarray-defaults
*
* @example
* import defaults from '@stdlib/ndarray-defaults';
*
* var o = defaults();
* // returns {...}
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import get from './get.js';


// MAIN //

setReadOnly( main, 'get', get );


// EXPORTS //

export default main;
